.quick-apply {
  //載入當頁var
  @import '../variable/QuickApplyVar';
  line-height: $line-height;
  background: $bgcolor;
  font-family: $font-family;

  //packadate.js套件高度控制
  .picker__holder {
    height: 250px;
  }

  .loading {
    @include block-center;
    position: absolute;
    z-index: 10;
    height: 100%;
    width: 100%;
    background: $load-bgcolor;
  }

  .nt-page {
    height: 100%;

    //定位地圖loading style
    .isgetLocationLoading {
      @include block-center;
      position: absolute;
      z-index: 10;
      width: 100%;
      height: 100%;
      background: $load-bgcolor;
    }

    .nt-page-header {
      position: relative;
      background: $bgcolor;
      height: 100px;

      .quick-back-btn {
        position: absolute;
        top: 20px;
        left: 20px;
        display: block;
        color: $back-btn-color;
      }

      .quick-title {
        font-weight: bold;
        color: $title-color;
        text-align: center;
        padding: 40px 40px 0 40px;
      }
    }

    .nt-page-body {
      height: calc(568px - 200px);
      background: $bgcolor;
      @include clearfix;

      p {
        margin-left: 5%;
        color: $text-color;
        padding: 10px;
      }

      .service-species,
      .service-type {
        li {
          float: left;
          width: 27.33333%;
          margin: 0 3% 4% 3%;
        }

        a {
          @include block-center;
          border: 2px solid $service-itemBd-color;
          background: $selector-bgcolor;
          @include box-shadow;
          font-weight: bold;
          border-radius: 5px;
          text-decoration: none;
          text-align: center;
          height: 50px;
          padding: 2px;
          overflow: hidden;
        }

        div {
          text-align: center;
          font-weight: bold;
        }

        .active-img {
          color: $selector-active-color;
          background: $selector-active-bgcolor;
          border: 0;
        }

        .active-text {
          color: $selector-active-color;
        }
      }

      .service-text-link {
        display: block;
        text-decoration: none;
        text-align: center;
        margin: 20px 25%;
        padding: 10px;
      }

      .reason-box {
        display: block;
        width: 90%;
        margin: 0 auto;
        padding: 10px;

        .reason-select {
          width: 70%;
          border: 0;
          border-radius: 5px;
          outline: none;
          height: 48px;
        }

        .reason-text {
          width: 25%;
          margin-left: 3%;
          height: 48px;
        }
      }

      .mic-animation {
        color: $mic-start-color;
        font-size: 15px;
      }

      .resetBtn {
        display: block;
        width: 50%;
        margin: 0 auto;
        border: 0;
        outline: none;
      }

      .setTime {
        display: block;
        width: 90%;
        margin: 0 auto;
        font-size: 20px;
        padding: 5px 10px;
        height: 48px;
        border: 0;
        border-radius: 5px;
      }

      .pos-btn {
        margin-left: 5%;
        border: 0;
        border-radius: 5px;
        padding: 5px;
        color: $pos-btn-color;
        background: $pos-btn-bgcolor;
        outline: none;
      }

      .user-pos,
      .user-name,
      .user-tel {
        padding: 5px;
        display: block;
        width: 90%;
        margin: 5px auto;
        border: 0;
        border-radius: 4px;
      }

      .set-key {
        margin-left: 5%;
        padding: 5px;
        width: 50%;
        border: 0;
        border-radius: 4px;
      }

      .get-key {
        margin-left: 5%;
        padding: 5px;
        color: $key-btn-color;
        background: $key-btn-bgcolor;
        font-weight: bold;
        border: 2px solid $key-btn-color;
        border-radius: 4px;
      }
    }

    .problem-textarea {
      height: 100%;

      textarea {
        width: 100%;
        resize: none;
        padding: 10px;
        height: 80%;
        border: 0;
      }

      input {
        width: 100%;
        padding: 10px;
        color: $next-btn-color;
        background: $next-btn-bgcolor;
        font-size: 20px;
        font-weight: bold;
        height: 20%;
        border: 0;
        border-radius: 4px;
      }
    }

    .nt-page-footer {
      background: $bgcolor;
      height: 100px;

      .next-btn,
      .submit-btn {
        display: block;
        width: 90%;
        margin: 0 auto;
        padding: 10px;
        color: $submit-btn-color;
        background: $submit-btn-bgcolor;
        border: 0;
        border-radius: 3px;
      }

      .disable {
        background: $disable-color;
      }
    }
  }
}