// 清除浮動
@mixin clearfix {

  &:before,
  &:after {
    content: "";
    display: table;
  }

  &:after {
    clear: both;
  }
}

//區塊內容居中
@mixin block-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

// box-shadow
@mixin box-shadow {
  box-shadow: 2px 1px 8px rgba(0, 0, 0, 0.4);
}

// 大小
@mixin size($width, $height) {
  width: $width;
  height: $height;
}

// 隱藏文字
@mixin hide-text {
  text-indent: 110%;
  white-space: nowrap;
  overflow: hidden;
}

// 限制字數 超多以...顯示
@mixin text-overflow() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// 熱門@media相關 數值太接近者先不設定
@mixin desktop {
  @media (max-width: 1280px) {
    @content;
  }
}

@mixin ipad {
  @media (max-width: 768px) {
    @content;
  }
}

@mixin Galaxy-s5-l {
  @media (max-width: 640px) {
    @content;
  }
}

@mixin iphone-6 {
  @media (max-width: 414px) {
    @content;
  }
}

@mixin Galaxy-s5 {
  @media (max-width: 360px) {
    @content;
  }
}

@mixin iphone-5 {
  @media (max-width: 320px) {
    @content;
  }
}