.account {
  //載入當頁var
  @import '../variable/accountVar';
  line-height: $line-height;

  .loading {
    position: absolute;
    display: flex;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-content: center;
  }

  .isOurAppIdPage {
    background: $bgcolor;
    width: 96%;
    margin: 2%;
    @include clearfix;

    h3 {
      float: left;
      display: block;
      width: 90%;
      text-align: center;
      color: $title-color;
      text-shadow: 1px 1px 1px #eee;
      background: $title-bgcolor;
      @include box-shadow;
      margin: 10px 5% 30px 5%;
      border: 0;
      border-radius: 3px;
    }

    .user-input {
      float: left;
      display: block;
      width: 80%;
      margin: 10px 0 30px 10%;
      @include clearfix;

      input {
        display: block;
        float: left;
        width: 90%;
        border: 0;
        border-bottom: 2px solid $input-borger-bmcolor;
        outline: none;
        padding: 8px;
        height: 39px;
        transition: border-bottom 0.3s;
      }

      i {
        margin-top: 11px;
        display: block;
        float: left;
        width: 10%;
      }

      .active {
        border-bottom: 2px solid $input-borger-bmcolor-active;
      }
    }

    .user-input input:focus {
      border-width: 3px;
    }

    .key-btn {
      float: left;
      border: 0;
      border-radius: 5px;
      outline: none;
      margin-left: 19%;
      padding: 10px;
      color: $key-btn-color;
      background: $key-btn-bgcolor;
    }

    .submit-btn {
      float: left;
      display: block;
      border: 0;
      border-radius: 5px;
      outline: none;
      width: 80%;
      margin: 50px 10% 20px 10%;
      padding: 10px;
      color: $submit-btn-color;
      background: $submit-btn-bgcolor;
      transition: background-color 0.3s;
    }

    .disable {
      background: $disable-color;
    }
  }
}