// 全域行距
$line-height:1.8;

//color
//disable顏色
$disable-color:gray;

// 網站整體寬度
$container-width:960px;

// font
$font-s:12px;
$font-m:14px;
$font-l:18px;

// 基準寬度
$gutter: 15px;

// 盒子陰影
.box-shadow {
  box-shadow: 2px 1px 8px rgba(0, 0, 0, 0.4);
}

// 刪除線
.line-through {
  text-decoration: line-through;
}

// 隱藏文字
.hide-text {
  text-indent: 110%;
  white-space: nowrap;
  overflow: hidden;
}

// 限制字數 超多以...顯示
.text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// 間距設定 0-20 每個參數間格5px
//m-(參數):x水平 y垂直 t l b r代表方向參數 沒加就是全部
