  .m-x-#{$i} {
    margin-left: $i * 1px;
    margin-right: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .m-y-#{$i} {
    margin-top: $i * 1px;
    margin-bottom: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .m-t-#{$i} {
    margin-top: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .m-l-#{$i} {
    margin-left: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .m-b-#{$i} {
    margin-bottom: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .m-r-#{$i} {
    margin-right: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .p-#{$i} {
    padding: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .p-x-#{$i} {
    padding-left: $i * 1px;
    padding-right: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .p-y-#{$i} {
    padding-top: $i * 1px;
    padding-bottom: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .p-t-#{$i} {
    padding-top: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .p-l-#{$i} {
    padding-left: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .p-b-#{$i} {
    padding-bottom: $i * 1px;
  }
}

@for $i from 0 through 100 {
  .p-r-#{$i} {
    padding-right: $i * 1px;
  }
}

